.sneakersTop {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-between;
  margin-bottom: 40px;
  align-items: center;
    @media (max-width: 644px) {
      justify-content: center;
    flex-direction: column;
    }
  .searchBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 250px;
    height: 45px;
    border: 2px solid #e4e4e4;
    border-radius: 10px;
    padding: 15px 15px 15px 18px;
    transition: border 0.4s ease;
    img {
      position: absolute;
      top: 10.5px;
      right: 10px;
      transform: rotate(45deg);
      transition: transform 0.3s ease;
      width: 20px;
      height: 20px;
      &:hover {
        transform: rotate(135deg);
      }
    }

    svg {
      justify-content: center;
      path {
        transition: stroke 0.4s ease;
      }
    }
    input {
      justify-content: start;
      width: 180px;
      padding-right: 40px;
      height: 40px;
      border: none;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #000000;
      margin-right: 4px;
    }
    input::placeholder {
      border: none;
    }
  }
  .searchBox:hover,
  .searchBox:active,
  .searchBox:focus {
    border: 2px solid #3cc755;
  }
  .searchBox:hover svg path,
  .searchBox:active svg path,
  .searchBox:focus svg path {
    stroke: #3cc755;
  }
}
